import "../styles/SinglePage.css";
import axios from "axios";
import { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import Currency from "../components/Currency";
import { IoCart } from "react-icons/io5";

const SinglePage = () => {
  const params = useParams();
  const [product, setProduct] = useState({});
  const [relatedProducts, setRelatedProducts] =useState([]);

  // get products
  const getProducts = async () => {
    try {
      const { data } = await axios.get(
        `/api/v1/product/get-product/${params.slug}`
      );
      setProduct(data?.product);
      getSimilarProducts(data?.product._id, data?.product.category._id)
    } catch (error) {
    }
  };

  useEffect(() => {
    if (params?.slug) getProducts();
  }, [params?.slug]);

  // get similar products
  const getSimilarProducts = async (pid,cid) => {
    try {
      const { data } = await axios.get(`/api/v1/product/related-product/${pid}/${cid}`);
      setRelatedProducts(data?.products);
    } catch (error) {
    }
  }

  return (
    <>
      <div className="single-prod-page">
        <div className="single-page">
          <div className="sp-img">
            <img
              src={`/api/v1/product/product-photo/${product._id}`}
              alt={product.name}
            />
          </div>

          <div className="product-details">
            <p className="category">{product?.category?.name}</p>
            <h2>{product.name}</h2>
            <p>{product.description}</p>
            <h3>
              <Currency price={product.price} />
            </h3>

            <Link className='sp-button-link'>
              <button className="sp-card-button">
                add to cart
                <IoCart className='sp-cart-icon'/>
              </button>
            </Link>
          </div>
        </div>

        <div className="similar">
          <h4>similar products</h4>
          {relatedProducts.length && <p>No Similar Products Available</p>}
          <div className="similar-prod">
          {relatedProducts?.map(p => {
              return (
                <>
                <Link to={`/singlepage/${p.slug}`} key={p._id} className='card-link'>
                  <div className="card">
                    <div className="card-img">
                      <img src={`/api/v1/product/product-photo/${p._id}`} alt={p.name} />
                    </div>
                    <div className="card-info">
                      <p className="text-title"> {p.name} </p>
                      <p className="text-body"> {p.description.substring(0,30)}... </p>
                    </div>
                    <div className="card-footer">
                      <span className="text-title"> {<Currency price={p.price} />} </span>
                      <Link className='card-button-link'>
                        <div className="card-button">
                          <IoCart className='cart-icon'/>
                        </div>
                      </Link>
                    </div>
                  </div>
                </Link>
                </>
              )
            })}
          </div>
        </div>
      </div>
    </>
  );
};

export default SinglePage;
